import React, {Component, Fragment} from 'react'
import {ConfigConsumer} from 'antd/lib/config-provider'
import PropTypes from "prop-types";
import Icon from "antd/lib/icon"
import { connect } from 'react-redux'
import Switch from "antd/lib/switch"
import Modal from "antd/lib/modal"
import Button from "antd/lib/button"
import CellBase from "./CellBase";
import isEqual from "lodash/isEmpty"
import {getResultByCell, getStateCode} from "../../selectors";
import {deleteResultByChannel} from "../../actions";

class ManualSwitch extends CellBase {
  state = {
    enable: true
  }
  onChange = (checked, e)=>{
    let {cell} = this.props
    if(checked){
      this.sendCmd("ds", cell.i)
      this.sendCmd("md", cell.i)
      this.sendCmd("rc", cell.i)
      this.sendCmd("en", cell.i)
      this.setState({enable: true})
    }else{
      this.sendCmd("ds", cell.i)
      this.setState({enable: false})
    }
  }

  handleReset = () => {
      this.props.deleteResultByChannel(this.props.cell.i)
  }

  renderManualSwitch = ({getPrefixCls}) => {
    const {
      prefixCls: customizePrefixCls,
      cell,
      stateCode,
      result,
    } = this.props
    const colors = ['#AAAAAA', '#FF851B','#FF4136','#0074D9', '#01FF70']
    const names = ["自动","手动", "急停", "使能", "去使能"]
    const color = colors[Math.floor(stateCode)%5]
    const name = names[Math.floor(stateCode)%5]
    const prefixCls = getPrefixCls('manual-switch', customizePrefixCls);
    const self = this
    const {enable} = this.state
    return <div
      className={`${prefixCls}`}
    >
      <div>
        <Switch
          checkedChildren="开"
          unCheckedChildren="关"
          checked={enable && result && result.return_code === 0}
          onChange={this.onChange}
          onClick={this.onChange}
        />
        <span>{stateCode===100?'使能':'1未使能'}</span>
      </div>
      <div>
        <Icon type="drag" />
        <span>托动</span>
      </div>
      <div>
        <Icon type="pushpin" />
        <span>保存点</span>
      </div>
      <div>
        <Icon type="save" />
        <span>保存轨迹</span>
      </div>
      <Modal
        visible={result && result.return_code !== 0}
        title="错误"
        onOk={this.handleReset}
        onCancel={this.handleReset}
        centered={true}
        footer={[
          <Button key="close" onClick={this.handleReset}>
            关闭
          </Button>,
        ]}
      >
        <span className={"danger"}>
          {result && (result.return_message?`${result.return_message} 返回码：${result.return_code} `:`返回码：${result.return_code}`)}</span>
      </Modal>
    </div>
  }

  render(){
    return <ConfigConsumer>{this.renderManualSwitch}</ConfigConsumer>
  }
}

ManualSwitch.propTypes = {
  cell: PropTypes.object.isRequired,
  stateCode: PropTypes.number,
}

function mapStateToProps(state, props) {
  return {
    // stateCode: getStateCode(state),
    result: getResultByCell(state, props)
  }
}

export default connect(
  mapStateToProps,
  {deleteResultByChannel}
)(ManualSwitch)

