import * as actions from '../actions'

const initialState = {
  resultsByChannel: {},
  websocketConnected: false,
  websocketError: null,
  channelsById: {},
  robotStatus: 4,
}

//可以处理中文
function utf8ArrayToStr(array) {
  let out, i, len, c
  let char2, char3

  out = ""
  len = array.length
  i = 0
  while(i < len) {
    c = array[i++]
    switch(c >> 4)
    {
      case 0: case 1: case 2: case 3: case 4: case 5: case 6: case 7:
      // 0xxxxxxx
      out += String.fromCharCode(c)
      break
      case 12: case 13:
      // 110x xxxx   10xx xxxx
      char2 = array[i++]
      out += String.fromCharCode(((c & 0x1F) << 6) | (char2 & 0x3F))
      break
      case 14:
        // 1110 xxxx  10xx xxxx  10xx xxxx
        char2 = array[i++]
        char3 = array[i++]
        out += String.fromCharCode(((c & 0x0F) << 12) |
          ((char2 & 0x3F) << 6) |
          ((char3 & 0x3F) << 0))
        break;
    }
  }

  return out
}



function analyzeBotData(buffer, channelsByID, anyRobot) {
  let dataView=new DataView(buffer);
  let cmdLength=dataView.getInt32(0);
  let cmdId=dataView.getInt32(4,true);
  let cmdCode=dataView.getFloat64(16,true);
  //如是没有channel的cmd数据存储
  if(!channelsByID[cmdCode]){
    return
  }
  try{
    let str = utf8ArrayToStr(new Uint8Array(buffer, 40))
    return {...JSON.parse(str), id: cmdCode, cmd_response_time: Date.now() - cmdCode}
  }catch (e) {
    console.error(e)
  }
}

export default (state = initialState, action) => {
  switch (action.type) {
    case actions.WEBSOCKET_CONNECTED:
      return {...state, websocketConnected: true}
    case actions.WEBSOCKET_DISCONNECTED:
      return {...state, websocketConnected: false}
    case actions.WEBSOCKET_ERROR:
      return {...state, websocketError: action.payload}
    case actions.UPDATE_MANUAL_MOTION_VEL:
      console.log(action)
      return {...state, manualMotionVel:action.vel}
    case actions.SEND_CMD:
    {
      let {id, channel} = action
      //如果channel被设定则会收集cmd结果
      if(!!channel){
        let channelsById = {...state.channelsById}
        channelsById[id] = channel
        return {...state, channelsById}
      }else{
        return state
      }
    }
    case actions.CMD_RESPONSE:
    {
      let {data} = action
      let channelsById = {...state.channelsById}
      let resultsByChannel = {...state.resultsByChannel}
      let result = analyzeBotData(data, state.channelsById, state.model)
      if(result){
        let channel = channelsById[result.id]
        delete channelsById[result.id]
        if(channel){
          resultsByChannel[channel] = result
        }
        return {...state, channelsById, resultsByChannel }
      }else{
        return state
      }
    }
    case actions.DELETE_RESULT_BY_CHANNEL:
    {
      let {channel} = action
      let resultsByChannel = {...state.resultsByChannel}
      delete resultsByChannel[channel]
      return {...state, resultsByChannel }
    }
    default:
      return state
  }
  return state
}