let cmdTimestamp = Date.now()

function action(type, payload = {}) {
  return {type, ...payload}
}

export const LOAD_CONFIG = 'LOAD_CONFIG'
export const CONFIG_FETCH_REQUEST = 'CONFIG_FETCH_REQUEST'
export const CONFIG_FETCH_FAILURE = 'CONFIG_FETCH_FAILURE'
export const CONFIG_FETCH_SUCCESS = 'CONFIG_FETCH_SUCCESS'
export const config = {
  request: () => action(CONFIG_FETCH_REQUEST, {}),
  success: (response) => action(CONFIG_FETCH_SUCCESS, {response}),
  failure: (error) => action(CONFIG_FETCH_FAILURE, {error}),
}
export const loadConfig = (requiredFields = []) => action(LOAD_CONFIG, {requiredFields})


export const UPDATE_DASHBOARD = 'UPDATE_DASHBOARD'
export const DASHBOARD_UPDATE_REQUEST = 'DASHBOARD_UPDATE_REQUEST'
export const DASHBOARD_UPDATE_FAILURE = 'DASHBOARD_UPDATE_FAILURE'
export const DASHBOARD_UPDATE_SUCCESS = 'DASHBOARD_UPDATE_SUCCESS'
export const dashboardUpdate = {
  request: (dashboard) => action(DASHBOARD_UPDATE_REQUEST, {dashboard}),
  success: (dashboard) => action(DASHBOARD_UPDATE_SUCCESS, {dashboard}),
  failure: (error) => action(DASHBOARD_UPDATE_FAILURE, {error}),
}
export const updateDashboard = (dashboard) => action(UPDATE_DASHBOARD, {dashboard})


export const CREATE_CELL = 'CREATE_CELL'
export const CELL_CREATE_REQUEST = 'CELL_CREATE_REQUEST'
export const CELL_CREATE_FAILURE = 'CELL_CREATE_FAILURE'
export const CELL_CREATE_SUCCESS = 'CELL_CREATE_SUCCESS'
export const cellCreate = {
  request: (dashboardId) => action(CELL_CREATE_REQUEST, {dashboardId}),
  success: (dashboardId, cell) => action(CELL_CREATE_SUCCESS, {dashboardId, cell}),
  failure: (error) => action(CELL_CREATE_FAILURE, {error}),
}
export const createCell = (dashboardId, cell) => action(CREATE_CELL, {dashboardId, cell})

export const DELETE_CELL = 'DELETE_CELL'
export const CELL_DELETE_REQUEST = 'CELL_DELETE_REQUEST'
export const CELL_DELETE_FAILURE = 'CELL_DELETE_FAILURE'
export const CELL_DELETE_SUCCESS = 'CELL_DELETE_SUCCESS'
export const cellDelete = {
  request: (dashboardId, cellId) => action(CELL_DELETE_REQUEST, {dashboardId, cellId}),
  success: (dashboardId, cell) => action(CELL_DELETE_SUCCESS, {dashboardId, cell}),
  failure: (error) => action(CELL_DELETE_FAILURE, {error}),
}
export const deleteCell = (dashboardId, cellId) => action(DELETE_CELL, {dashboardId, cellId})


export const UPDATE_ROUTER_STATE = 'UPDATE_ROUTER_STATE'
export const NAVIGATE =  'NAVIGATE'
export const RESET_ERROR_MESSAGE = 'RESET_ERROR_MESSAGE'
export const RESET_LOADING_MESSAGE = 'RESET_LOADING_MESSAGE'

export const navigate = pathname => action(NAVIGATE, {pathname})
export const resetErrorMessage = () => action(RESET_ERROR_MESSAGE)
export const updateRouterState = state => action(UPDATE_ROUTER_STATE, {state})

export const START_WEBSOCKET = 'START_WEBSOCKET'
export const STOP_WEBSOCKET = 'STOP_WEBSOCKET'
export const SEND_CMD = 'SEND_CMD'
export const CMD_RESPONSE = 'CMD_RESPONSE'
export const WEBSOCKET_CONNECTED = 'WEBSOCKET_CONNECTED'
export const WEBSOCKET_DISCONNECTED = 'WEBSOCKET_DISCONNECTED'
export const WEBSOCKET_ERROR = 'WEBSOCKET_ERROR'
export const startWebsocket = (url) => action(START_WEBSOCKET,{url})
export const stopWebsocket = () => action(STOP_WEBSOCKET, {stopped:true})
export const wsConnected = () => action(WEBSOCKET_CONNECTED)
export const wsDisconnected = () => action(WEBSOCKET_DISCONNECTED)
export const wsError = (error) => action(WEBSOCKET_ERROR, {error})
export const sendCmd = (cmd, channel) => {
  let now = Date.now()
  if(now <= cmdTimestamp){
    cmdTimestamp = cmdTimestamp + 1
  }else{
    cmdTimestamp = now
  }
  return action(SEND_CMD, {cmd, id:cmdTimestamp, channel})
}

export const UPDATE_MANUAL_MOTION_VEL = "UPDATE_MANUAL_MOTION_VEL"
export const updateManualMotionVel = (vel) => action(UPDATE_MANUAL_MOTION_VEL, {vel})
export const DELETE_RESULT_BY_CHANNEL = "DELETE_RESULT_BY_CHANNEL"
export const deleteResultByChannel = (channel) => action(DELETE_RESULT_BY_CHANNEL, {channel})
