import React, {Component, Fragment} from 'react'
import {ConfigConsumer} from 'antd/lib/config-provider'
import PropTypes from "prop-types";
import { connect } from 'react-redux'
import Slider from 'antd/lib/slider'
import {
  updateManualMotionVel
} from '../../actions'

class ManualMotionVel extends Component {
  componentDidMount() {
    let {vel, updateManualMotionVel} = this.props
    let option = this.getVelOption()
    if((!!option || option === 0) && !vel){
      updateManualMotionVel(option)
    }
  }

  getVelOption = () => {
    const { cell } = this.props
    if(cell.options) {
      let fo = cell.options.filter(o=>o.name==='manualMotionVel')
      if(fo.length>0){
        try{
          return parseFloat(fo[0].value)
        }catch (e) {
          console.error(e)
        }
      }
    }
  }
  renderManualMotionVel = ({getPrefixCls}) => {
    const {
      prefixCls: customizePrefixCls,
      cell,
    } = this.props
    const prefixCls = getPrefixCls('manual-motion-vel', customizePrefixCls);
    const option = this.getVelOption()
    return <div
      className={`${prefixCls}`}
    >
      <Slider
        tooltipVisible={true}
        min={0}
        max={100}
        tipFormatter={(value)=>`${value}%`}
        value={this.props.vel || option}
        onChange={(value)=>{
          this.props.updateManualMotionVel(value)
          let copyCell = {...this.props.cell}
          if(!copyCell.options || copyCell.options.length === 0){
            copyCell.options = [{name:"manualMotionVel", value: `${value}`}]
          }else{
            copyCell.options = [...copyCell.options]
            let matched = false
            copyCell.options.forEach((o, i)=>{
              if(o.name === "manualMotionVel"){
                copyCell.options[i] = {name:"manualMotionVel", value: `${value}`}
                matched = true
              }
            })
            if(!matched){
              copyCell.options.push({name:"manualMotionVel", value: `${value}`})
            }
          }
          console.log(copyCell)
          this.props.onCellUpdate(copyCell)
        }}
        getTooltipPopupContainer={()=>document.getElementById("dashboard")}
      />
    </div>
  }

  render(){
    return <ConfigConsumer>{this.renderManualMotionVel}</ConfigConsumer>
  }
}

ManualMotionVel.propTypes = {
  cell: PropTypes.object.isRequired,
  vel: PropTypes.number,
  updateManualMotionVel: PropTypes.func.isRequired,
  onCellUpdate: PropTypes.func.isRequired
}

function mapStateToProps(state) {
  return {vel: state.robot.manualMotionVel}
}

export default connect(
  mapStateToProps,
  {
    updateManualMotionVel
  },
)(ManualMotionVel)

