import React from 'react'
import PropTypes from "prop-types";
import Input  from 'antd/lib/input'
import CellBase from "./CellBase";
import { connect } from 'react-redux'
import Select from "antd/lib/select"
import RealtimeChartChannel from "../containers/RealtimeChartChannel";
import {
  getMemoizeChannelNames,
} from "../../selectors";

const Option = Select.Option


class RealtimeChart extends CellBase {
   handleChange = (value) => {
     const options = this.getOptions()
     this.updateOptions({...options, channel: value})
  }
  renderContent = ({getPrefixCls}) => {
    const {
      prefixCls: customizePrefixCls,
      cell,
      channels
    } = this.props
    const options = this.getOptions()
    const prefixCls = getPrefixCls('realtime-chart', customizePrefixCls);
    let self = this
    let value = ""
    if(channels && channels.indexOf(options.channel) !== -1){
      value = options.channel
    }
    return <div
      className={`${prefixCls} ${!(channels && channels.length > 0) && prefixCls+"-no-data"}`}
    >
      {!(channels && channels.length > 0) && <span>无实时数据</span>}
      {!!(channels && channels.length > 0) && <Select
        style={{width: "100%"}}
        size={"small"}
        placeholder={"选择DataKey"}
        onSelect={this.handleChange}
        value={value}
      >
        {
          channels.map(c => <Option key={c} value={c}>{c.toUpperCase()}</Option>)
        }
      </Select>
      }
      {
        !!(channels && channels.length > 0 && options.channel) && <RealtimeChartChannel
          key={options.channel}
          channel={options.channel}
          options={options}
          updateOptions={this.updateOptions}
          prefixCls={prefixCls}
        />
      }
      </div>
  }
}

RealtimeChart.propTypes = {
  channels: PropTypes.array.isRequired
}

const makeMapStateToProps = () => {
  const mapStateToProps = (state, props) => {
    return {
      channels: getMemoizeChannelNames(state),
    }
  }
  return mapStateToProps
}

export default connect(
  makeMapStateToProps,
)(RealtimeChart)

