import React, {Component, Fragment} from 'react'
import ReactEcharts from "echarts-for-react";
import echarts from "echarts"
import PropTypes from "prop-types";
import { connect } from 'react-redux'
import {
} from "../../selectors";
import {makeGetDataKeysByChannel} from "../../selectors";
import Select from "antd/lib/select"
import {makeGetCachedDataByChannelAndKey} from "../../selectors";
const Option = Select.Option


class RealtimeChartChannelAndDataKey extends Component {
  componentDidMount() {
    console.log('RealtimeChartChannelAndDataKey componentDidMount')

  }

  render() {
    const {dataKey, channel, data, options}  = this.props
    let getOptions = null
    if(data && data.length > 0){
      getOptions = {
        title: {
          text:channel +' '+ dataKey
        },
        xAxis: {
          splitLine:{show: false},//去除网格线
          type: "category",
          data: data.map(d=>d[0])
        },
        yAxis: {
          splitLine:{show: false},//去除网格线
          type: 'value'
        },
        series: data[0][1].map((d, i)=>({
          name: `${dataKey}${i}`,
          // smooth:true,
          data: data.map(d=>d[1][i]),
          symbol: 'none',
          // sampling: 'average',
          type: 'line'
        }))
      }
    }
    console.log(getOptions)
    return <Fragment>
      {
        getOptions && <ReactEcharts
          option={getOptions}
          notMerge={true}
          lazyUpdate={true}
        />
      }
    </Fragment>
  }
}

RealtimeChartChannelAndDataKey.propTypes = {
  updateOptions:PropTypes.func.isRequired,
  prefixCls: PropTypes.string.isRequired,
  channel: PropTypes.string.isRequired,
  dataKey: PropTypes.string.isRequired,
  // data: PropTypes.array.isRequired,
}

const makeMapStateToProps = () => {
  const getCachedDataByChannelAndKey = makeGetCachedDataByChannelAndKey()
  const mapStateToProps = (state, props) => {
    return {
      data: getCachedDataByChannelAndKey(state, props.channel, props.dataKey)
    }
  }
  return mapStateToProps
}

export default connect(
  makeMapStateToProps,
)(RealtimeChartChannelAndDataKey)

