import { createSelector, createSelectorCreator } from 'reselect'
import memoize from 'lodash/memoize'
import isNumber from 'lodash/isNumber'
import isArray from 'lodash/isArray'

const hashFn = (...args) => {
  return args.reduce(
    (acc, val) => acc + '-' + JSON.stringify(val),
    ''
  )
}
const hashEqualSelectorCreator = createSelectorCreator(memoize, hashFn)


export const getResultByCell = (state, props) => state.robot.resultsByChannel[props.cell.i]
export const getResultByChannel = (state, channel) => state.robot.resultsByChannel[channel]
export const getMemoizeResultByCell = hashEqualSelectorCreator(
  getResultByCell,
  (result)=>result
)
export const getMemoizeResultByChannel = hashEqualSelectorCreator(
  getResultByChannel,
  (result)=>result
)

export const getChannelNames = state => Object.keys(state.robot.resultsByChannel)
export const getMemoizeChannelNames = hashEqualSelectorCreator(
  getChannelNames,
  (result)=>result
)

export const makeGetDataKeysByChannel = () =>{
  const getDataKeysByChannel = createSelector(
    getMemoizeResultByChannel,
    result => {
      if(result){
        return Object.keys(result).filter(key=>{
          if(isNumber(result[key])){
            return true
          }
          if(isArray(result[key]) && isNumber(result[key][0])){
            return true
          }
        })
      }
    }
  )
  return hashEqualSelectorCreator(
    getDataKeysByChannel,
    r=>r
  )
}
export const getDataByChannelAndKey = (state, channel, key) => {
  const result = getResultByChannel(state, channel)
  if(result && result[key]){
    if(isNumber(result[key])){
      return [new Date(result.id), [result[key]]]
    }else{
      return [new Date(result.id), result[key]]
    }
  }
}
export const makeGetCachedDataByChannelAndKey = (cacheSize=10000) =>{
  let cachedResults = []
  return hashEqualSelectorCreator(
    getDataByChannelAndKey,
    result=>{
      console.log(result)
      if(result){
        cachedResults.push(result)
      }
      let length = cachedResults.length;
      if(length > cacheSize){
        cachedResults.splice( 0, length - cacheSize);
      }
      return [...cachedResults]
    }
  )
}


