import React, {Component, Fragment} from 'react'
import PropTypes from "prop-types";
import { connect } from 'react-redux'
import {
} from "../../selectors";
import {makeGetDataKeysByChannel} from "../../selectors";
import RealtimeChartChannelAndDataKey from "./RealtimeChartChannelAndDataKey";
import Select from "antd/lib/select"
const Option = Select.Option

class RealtimeChartChannel extends Component {
  handleSelect = (value) => {
    const {options, updateOptions} = this.props
    updateOptions({...options, dataKey: value})
  }
  componentDidMount() {
    console.log('RealtimeChartChannel componentDidMount')
  }

  render() {
    console.log('RealtimeChartChannel render')

    const {
      dataKeys,
      options,
      updateOptions,
      channel,
      prefixCls
    }  = this.props
    let value = ""
    if(dataKeys && dataKeys.indexOf(options.dataKey) !== -1){
      value = options.dataKey
    }
    return <Fragment>
      {!!(dataKeys && dataKeys.length > 0) && <Select
        style={{width: "100%"}}
        size={"small"}
        onSelect={this.handleSelect}
        value={value}
        placeholder={"选择DataKey"}
      >
        {
          dataKeys.map(c => <Option key={c} value={c}>{c.toUpperCase()}</Option>)
        }
      </Select>
      }
      {!!(dataKeys && dataKeys.length > 0 && value) && <RealtimeChartChannelAndDataKey
        key={(channel+value).replace(" ", "_")}
        dataKey={value}
        channel={channel}
        updateOptions={updateOptions}
        options={options}
        prefixCls={prefixCls}
        />
      }
    </Fragment>
  }
}

RealtimeChartChannel.propTypes = {
  updateOptions:PropTypes.func.isRequired,
  prefixCls: PropTypes.string.isRequired,
  channel: PropTypes.string.isRequired,
  dataKeys: PropTypes.array,
}

const makeMapStateToProps = () => {
  const getDataKeysByChannel = makeGetDataKeysByChannel()
  const mapStateToProps = (state, props) => {
    return {
      dataKeys: getDataKeysByChannel(state, props.channel)
    }
  }
  return mapStateToProps
}

export default connect(
  makeMapStateToProps,
)(RealtimeChartChannel)

