import React from 'react'
import {ConfigConsumer} from 'antd/lib/config-provider'
import PropTypes from "prop-types";
import Radio from "antd/lib/radio"
import Row from "antd/lib/row"
import Select from "antd/lib/select"
import Button from "antd/lib/button"
import InputNumber  from 'antd/lib/input-number'
import Input  from 'antd/lib/input'
import { connect } from 'react-redux'
import {
  sendCmd
} from '../../actions'
import {getMotionPos} from "../../reducers/selectors";
import CellBase from "./CellBase";
import {getMemoizeEndPE, getManualMotionVel} from "../../selectors";
const {Option} = Select
const RadioGroup = Radio.Group
const InputGroup = Input.Group

class JogCoordinate extends CellBase {
  state={
    mCoordinateRadio:0,
    mCoordinate:0,
  }
  onMCoordinateChange(e){
    let mCoordinate = this.state.mCoordinate
    if(e.target.value === 0){
      mCoordinate = 0
    }else if(mCoordinate === 0){
      mCoordinate = e.target.value
    }
    this.setState({
      mCoordinateRadio: e.target.value,
      mCoordinate,
    });
  }
  handleSelectChange(value){
    this.setState({
      mCoordinate: value,
    });
  }
  renderJogCoordinate = ({getPrefixCls}) => {
    const {
      prefixCls: customizePrefixCls,
      endPE,
      cell,
    } = this.props
    const prefixCls = getPrefixCls('jog-coordinate', customizePrefixCls);
    const radioStyle = {
      display: 'block',
      height: '30px',
      lineHeight: '30px',
    };
    const names = ['X', 'Y', 'Z', 'RX', 'RY', 'RZ']
    const types = ['x', 'y', 'z', 'rx', 'ry', 'rz']
    return <div
      className={`${prefixCls}`}
    >
      <Row>
        <RadioGroup onChange={this.onMCoordinateChange.bind(this)} value={this.state.mCoordinateRadio}>
          <Radio style={radioStyle} value={0}>
            世界坐标系
          </Radio>
          <Radio style={radioStyle} value={1}>
            <Select
              onChange={this.handleSelectChange.bind(this)}
              value={!this.state.mCoordinate?1:this.state.mCoordinate}
              disabled={!this.state.mCoordinate}
            >
              <Option value={1}>
                末端坐标系 1
              </Option>
              <Option value={2}>
                末端坐标系 2
              </Option>
            </Select>
          </Radio>
        </RadioGroup>
      </Row>
      {
        names.map((name, index)=>(<Row key={name}>
          <Button
            type="primary"
            size={"large"}
            onClick={()=>{
              this.getSendCmdFn(
                `j${types[index]} --vel_percent=${this.props.vel} --direction=-1 --cor=${this.state.mCoordinate} `,
                cell.i
              )()
            }}
            onMouseDown={()=>{
              this.getOnMouseDownFn(
                `j${types[index]} --vel_percent=${this.props.vel} --direction=-1 --cor=${this.state.mCoordinate} `,
                cell.i
              )()
            }}
            onMouseUp={()=>{
              this.getOnMouseUpFn()()
            }}
          >{name}-</Button>
            <Button
              type="primary"
              size={"large"}
              // onClick={()=>{
              //   this.getSendCmdFn(
              //     `j${types[index]} --vel_percent=${this.props.vel} --direction=1 --cor=${this.state.mCoordinate} `,
              //     cell.i
              //   )()
              // }}
              onMouseDown={()=>{
                this.getOnMouseDownFn(
                  `j${types[index]} --vel_percent=${this.props.vel} --direction=1 --cor=${this.state.mCoordinate} `,
                  cell.i
                )()
              }}
              onMouseUp={()=>{
                this.getOnMouseUpFn()()
              }}>{name}+</Button>
          </Row>)
        )
      }
      <Row className={`${prefixCls}-end_pe`}>
        <div>
          <span>X</span>
          <span>{!!endPE && (!!endPE[0] || endPE[0]===0)?endPE[0].toFixed(3):'null'}</span>
        </div>
        <div>
          <span>Y</span>
          <span>{!!endPE && (!!endPE[1] || endPE[1]===0)?endPE[1].toFixed(3):'null'}</span>
        </div>
        <div>
          <span>Z</span>
          <span>{!!endPE && (!!endPE[2] || endPE[2]===0)?endPE[2].toFixed(3):'null'}</span>
        </div>
      </Row>
      <Row className={`${prefixCls}-end_pe`}>
        <div>
          <span>RX</span>
          <span>{!!endPE && (!!endPE[3] || endPE[3]===0)?endPE[3].toFixed(3):'null'}</span>
        </div>
        <div>
          <span>RY</span>
          <span>{!!endPE && (!!endPE[4] || endPE[4]===0)?endPE[4].toFixed(3):'null'}</span>
        </div>
        <div>
          <span>RZ</span>
          <span>{!!endPE && (!!endPE[5] || endPE[5]===0)?endPE[5].toFixed(3):'null'}</span>
        </div>
      </Row>
    </div>
  }

  render(){
    return <ConfigConsumer>{this.renderJogCoordinate}</ConfigConsumer>
  }
}

JogCoordinate.propTypes = {
  cell: PropTypes.object.isRequired,
  sendCmd: PropTypes.func.isRequired,
  vel: PropTypes.number,
  endPE: PropTypes.array,
}

function mapStateToProps(state) {
  return {
    vel: getManualMotionVel(state),
    endPE: getMemoizeEndPE(state)
  }
}

export default connect(
  mapStateToProps,
  {
    sendCmd,
  },
)(JogCoordinate)

