import React, {Component, Fragment} from 'react'
import {ConfigConsumer} from 'antd/lib/config-provider'
import PropTypes from "prop-types";
import LayoutCell from "./LayoutCell";
import CellByTypes from "../cells";
import { connect } from 'react-redux'
import {getCommandSendDelay, getCommandSendInterval} from "../../selectors";
const Unknown = CellByTypes.Unknown
class Layout extends Component {
  constructor(props){
    super(props)
    this.cellByTypes = {}
    let self = this
    Object.keys(CellByTypes).forEach(key=>{
      self.cellByTypes[key.toLowerCase()]=CellByTypes[key]
    })
  }
  componentDidMount() {
    console.error("Layout componentDidMount")
  }

  toggleVisType = () =>{
    // TODO 切换CELL类型
  }
  renderLayout = ({getPrefixCls}) => {
    const {
      prefixCls: customizePrefixCls,
      isEditable,
      cell,
      onCloneCell,
      onDeleteCell,
      sendCmd,
      onCellUpdate,
    } = this.props
    const prefixCls = getPrefixCls('layout', customizePrefixCls);
    return <LayoutCell
      cell={cell}
      onCellUpdate={onCellUpdate}
      onDeleteCell={onDeleteCell}
      sendCmd={sendCmd}
      onCloneCell={onCloneCell}
      toggleVisType={this.toggleVisType.bind(this)}
      isEditable={isEditable}
    >
      {this.renderLayoutBody(prefixCls)}
    </LayoutCell>
  }

  renderLayoutBody(prefixCls){
    const {
      cell,
      onCellUpdate,
      sendCmd,
      commandSendInterval,
      commandSendDelay
    } = this.props
    if(!cell.type || !this.cellByTypes[cell.type.toLowerCase()]){
      return <Unknown cell={cell} onCellUpdate={onCellUpdate} sendCmd={sendCmd}/>
    }else{
      const CellComponent = this.cellByTypes[cell.type.toLowerCase()]
      return <CellComponent
        cell={cell}
        onCellUpdate={onCellUpdate}
        sendCmd={sendCmd}
        commandSendInterval={commandSendInterval}
        commandSendDelay={commandSendDelay}
      />
    }
  }

  render(){
    console.log('Layout render()')
    return <ConfigConsumer>{this.renderLayout}</ConfigConsumer>
  }
}

Layout.propTypes = {
  cell: PropTypes.object.isRequired,
  onDeleteCell: PropTypes.func.isRequired,
  onCellUpdate: PropTypes.func.isRequired,
  onCloneCell: PropTypes.func.isRequired,
  onZoom: PropTypes.func.isRequired,
  sendCmd: PropTypes.func.isRequired,
  isEditable: PropTypes.bool.isRequired,
}


function mapStateToProps(state, props) {
  return {
    commandSendInterval: getCommandSendInterval(state),
    commandSendDelay: getCommandSendDelay(state)
  }
}

export default connect(
  mapStateToProps,
)(Layout)

