import { schema, normalize } from 'normalizr'
import { camelizeKeys } from 'humps'
import has from 'lodash/has'
import { isEmpty } from 'lodash/isEmpty'
import 'isomorphic-fetch'


function callGetApi(endpoint) {
  return fetch(endpoint)
    .then(response =>
      response.json().then(json => ({ json, response }))
    ).then(({ json, response }) => {
      if (!response.ok) {
        return Promise.reject(json)
      }
      return json
    })
    .then(
      response => ({response}),
      error => ({error: error.message || 'Something bad happened'})
    )
}

function callPutApi(endpoint, data) {
  return fetch(endpoint, {
    method: 'PUT',
    headers: {
      'Content-Type': 'application/json',
    },
    body: JSON.stringify(data)
  })
    .then(response =>
      response.json().then(json => ({ json, response }))
    ).then(({ json, response }) => {
      if (!response.ok) {
        return Promise.reject(json)
      }
      return json
    })
    .then(
      response => ({response}),
      error => ({error: error.message || 'Something bad happened'})
    )
}

function callPostApi(endpoint, data) {
  return fetch(endpoint, {
    method: 'POST',
    headers: {
      'Content-Type': 'application/json',
    },
    body: JSON.stringify(data)
  })
    .then(response =>
      response.json().then(json => ({ json, response }))
    ).then(({ json, response }) => {
      if (!response.ok) {
        return Promise.reject(json)
      }
      return json
    })
    .then(
      response => ({response}),
      error => ({error: error.message || 'Something bad happened'})
    )
}

function callDeleteApi(endpoint, data) {
  return fetch(endpoint, {
    method: 'DELETE',
    headers: {
      'Content-Type': 'application/json',
    },
    body: JSON.stringify(data)
  })
    .then(response =>
      response.json().then(json => ({ json, response }))
    ).then(({ json, response }) => {
      if (!response.ok) {
        return Promise.reject(json)
      }
      return json
    })
    .then(
      response => ({response}),
      error => ({error: error.message || 'Something bad happened'})
    )
}

// api services
export const fetchConfig = () => callGetApi(`/api/config`)
export const updateDashboard = (dashboard) => callPutApi(`/api/dashboards/${dashboard.i}`, dashboard)
export const createCell = (dashboardId, cell) => callPostApi(`/api/dashboards/${dashboardId}/cells`, {...cell})
export const deleteCell = (dashboardId, cellId) => callDeleteApi(`/api/dashboards/${dashboardId}/cells/${cellId}`, {})

