import React, {Component, Fragment} from 'react'
import {ConfigConsumer} from 'antd/lib/config-provider'
import PropTypes from "prop-types";
import Icon from "antd/lib/icon"

class CommandButton extends Component {

  renderCommandButton = ({getPrefixCls}) => {
    const {
      prefixCls: customizePrefixCls,
      cell,
    } = this.props
    const prefixCls = getPrefixCls('command-button', customizePrefixCls);
    return <div
      className={`${prefixCls}`}
    >
      <Icon type="deployment-unit" style={{fontSize:50}}/>
    </div>
  }

  render(){
    return <ConfigConsumer>{this.renderCommandButton}</ConfigConsumer>
  }
}

CommandButton.propTypes = {
  cell: PropTypes.object.isRequired,
}

export default CommandButton