import React, {Component} from 'react'
import {ConfigConsumer} from 'antd/lib/config-provider'
import PropTypes from "prop-types";

class CellBase extends Component {
  interval = null
  timeout = null
  componentDidMount() {
    console.log("CellBase Mount()")
  }
  sendCmd = (cmd, channel) => {
    let {sendCmd, cell} = this.props
    sendCmd(cmd, channel || cell.i)
  }
  getSendCmdFn = (cmd, channel) => {
    return (()=>{
      this.sendCmd(cmd, channel)
    }).bind(this)
  }
  getOnMouseDownFn = (cmd, channel) => {
    return (()=>{
      if(this.interval){
        clearInterval(this.interval)
      }
      const {commandSendInterval, commandSendDelay} = this.props
      let fn = this.sendCmd.bind(this, cmd, channel)
      let self = this
      self.timeout = setTimeout(()=>{
        self.interval = setInterval(fn, commandSendInterval)
      }, commandSendDelay)
    }).bind(this)
  }
  getOnMouseUpFn = () => {
    return (()=>{
      if(this.timeout){
        clearTimeout(this.timeout)
      }
      if(this.interval){
        clearInterval(this.interval)
      }
    }).bind(this)
  }
  getOptions = () => {
    let {cell} = this.props
    if(!cell.options || cell.options.length === 0){
      return {}
    }else{
      return cell.options.reduce((options, o)=>{
        options[o.name] = o.value
        return options
      },{})
    }
  }
  updateOptions = options => {
    let cell = {...this.props.cell}
    cell.options = Object.keys(options).map(key=>{
      return {name: key, value: options[key].toString()}
    })
    this.props.onCellUpdate(cell)
  }

  renderContent = () => {
    throw new Error("renderComponent not implemented")
  }

  render(){
    console.log('CellBase render()')
    return <ConfigConsumer>{this.renderContent}</ConfigConsumer>
  }
}

CellBase.propTypes = {
  cell: PropTypes.object.isRequired,
  sendCmd: PropTypes.func.isRequired,
  onCellUpdate: PropTypes.func.isRequired,
  commandSendInterval: PropTypes.number.isRequired,
  commandSendDelay: PropTypes.number.isRequired
}

export default CellBase

