import * as ActionTypes from '../actions'

export default (state = {}, action) => {
  switch (action.type) {
    case ActionTypes.CONFIG_FETCH_REQUEST:
      return {}
    case ActionTypes.CONFIG_FETCH_FAILURE:
      return {}
    case ActionTypes.CONFIG_FETCH_SUCCESS:
      return {...state, ...action.response}
    case ActionTypes.UPDATE_DASHBOARD:
    {
      let {dashboard} = action
      let interfaceRoot = {...state.interfaceRoot}
      interfaceRoot.dashboards = [...interfaceRoot.dashboards]
      interfaceRoot.dashboards.forEach((d, i)=>{
        if(d.i === dashboard.i){
          interfaceRoot.dashboards[i] = {...interfaceRoot.dashboards[i], ...dashboard}
        }
      })
      return {...state, interfaceRoot}
    }
    case ActionTypes.DASHBOARD_UPDATE_SUCCESS:
    {
      let {dashboard} = action
      let interfaceRoot = {...state.interfaceRoot}
      interfaceRoot.dashboards = [...interfaceRoot.dashboards]
      interfaceRoot.dashboards.forEach((d, i)=>{
        if(d.i === dashboard.i){
          interfaceRoot.dashboards[i] = {...interfaceRoot.dashboards[i], ...dashboard}
        }
      })
      return {...state, interfaceRoot}
    }
    case ActionTypes.CELL_CREATE_SUCCESS:
    {
      let {dashboardId, cell} = action
      let interfaceRoot = {...state.interfaceRoot}
      interfaceRoot.dashboards = [...interfaceRoot.dashboards]
      interfaceRoot.dashboards.forEach((d, i)=>{
        if(d.i === dashboardId){
          interfaceRoot.dashboards[i] = {...interfaceRoot.dashboards[i]}
          interfaceRoot.dashboards[i].cells = [
            ...interfaceRoot.dashboards[i].cells,
            cell
          ]
        }
      })
      return {...state, interfaceRoot}
    }
    case ActionTypes.CELL_DELETE_SUCCESS:
    {
      let {dashboardId, cell} = action
      let interfaceRoot = {...state.interfaceRoot}
      interfaceRoot.dashboards = [...interfaceRoot.dashboards]
      interfaceRoot.dashboards.forEach((d, i)=>{
        if(d.i === dashboardId){
          interfaceRoot.dashboards[i] = {...interfaceRoot.dashboards[i]}
          interfaceRoot.dashboards[i].cells = interfaceRoot.dashboards[i].cells.filter(c => c.i !== cell.i)
        }
      })
      return {...state, interfaceRoot}
    }
    default:
      return state
  }
  return state
}